.footer{
    background-color: #222;
    color: #fff;
    padding: 2em;
    text-align: center;
}

.social_list{
    display:flex;
    justify-content: center;
    list-style: none;
}

.social_list li{
    margin: 0 1em;
}

.social_list li:hover{
    color:#ffbb33;
    cursor:pointer;
}

.social_list svg{
    font-size: 1.5em;
}

.copy_right{
    margin-top: 2em;
}

.copy_right span{
    font-weight: bold;
    color:#ffbb33;
}

.creditosAluno{
    max-width: 200px;
} 

.p_aluno{
    margin-bottom: 2em;
}