.container{
    max-width: 1200px;
    display: flex;
    justify-content: space-between;
    margin: 0 auto;
    padding: 0 2em;
    flex-wrap: wrap;
    /* border: 2px solid yellow; */
}

.min_height{
    min-height: 75%;
}

.start{
    justify-content: flex-start;
    padding: 0;
}

.column{
    flex-direction:  column;
    justify-content: flex-start;
    padding: 0px;
}

.container_Footer{
    justify-content: center;
    align-items: center;
    font-size: 0.7em;
    gap: 10%;
}
